.card__loading {
  display: flex;
  width: 100%;
  margin-bottom: 2rem;
  padding: 1rem;
  box-shadow: 0 0 10px #ccc;
  border-radius: 1rem;
  /* border: 1px solid steelblue; */
}

.card_loading__left {
  height: 100%;
  /* border: 1px solid tomato; */
}

.card_loading__image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #eee;
}

.card_loading__right {
  max-width: 450px;
  width: 100%;
  height: 100%;
  padding: 0 1rem;
  /* border: 1px solid darkred; */
}

.card_loading__title {
  width: 100%;
  height: 20px;
  margin-bottom: 10px;
  border-radius: 1rem;
  background-color: #eee;
}

.card_loading__paragraph {
  width: 80%;
  height: 10px;
  margin-bottom: 10px;
  border-radius: 1rem;
  background-color: #eee;
}

.card_loading__half__paragraph {
  width: 50%;
  height: 10px;
  border-radius: 1rem;
  background-color: #eee;
}
